@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;700&display=swap);
:root{
  --color-black: #000;
  --color-white: #FFF;
  --color-blue: #0F56B3;
  --color-gray: #FBFBFB;
  --color-dark-gray: #1A1A1A;
}

*{
  font-family: 'Roboto Condensed', sans-serif;
}

body {
  margin: 0;
}

@media screen and (max-width: 850px){
  .main{
    background-position-y: -48px !important;
  }

  .main-content {
      padding-top: 275px !important;
  }

  .main-content > .time-container{
    box-sizing: border-box;
    display: flex;
    padding: 0 16px;
    justify-content: center;
    width: 60%;
  }

  .main-content .time-container article{
    margin-right: 7.65px;
  }

  .main-content .time-container article .data-time{
    align-items: center;
    border-radius: 5.1px;
    display: flex;
    padding: 10.2px 5.1px;
    justify-content: center;
    height: 68.21px;
    width: 58.01px;
  }

  .main-content .time-container article .data-time h2{
    font-size: 40px;
  }

  .main-content .time-container article p{
    font-size: 12px;
    margin-top: 7.65px;
  }

}
.time-container{
  margin: auto;
  width: 800px;
}

.time-container > article{
  text-align: center;
  color: var(--color-white);
  display: inline-block;
  position: relative;
  margin-right: 24px;
  z-index: 20;
}

.time-container > article:last-of-type{
  margin-right: 0;
}

.time-container article > .data-time{
  background: var(--color-dark-gray);
  border-radius: 16px;
  padding: 32px 16px;
}

.time-container article > .data-time > h2{
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 128px;
}

.time-container article > p{
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 300;
  font-size: 40px;
}

.time-container article > .data-time > h2,
.time-container article > p{
  margin: 0;
}

.time-container article > p{
  margin-top: 24px;
}
.main{
  background: url(/countdown/static/media/bg.645a3d19.jfif);
  background-position-y: -480px;
  background-size: cover;
  margin: auto;
  min-height: 100vh;
  position: relative;
}

.main::after{
  content: "";
  background: var(--color-black);
  z-index: 10;
  bottom: 0;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  opacity: .8;
  z-index: 10;
}

.main-content{
  padding-top: 242px;
}
